<template>
    <div class="relative">
      <div 
      class="flex items-center rounded-lg overflow-hidden border"
      :class="{
        'border-grey-01 focus-within:border-grey-04': !error,
        'border-red-base': error
      }"
      >
        <input
          type="text"
          v-model="inputValue"
          class="w-full px-4 outline-none border-none"
          :disabled="disabled"
          :placeholder="placeholder"
          :ref="ref"
          style="min-height: 38px;"
          @input="handleInput"
          @focus="isFocused = true"
          @blur="onBlur"
        />
        <div class="flex items-center pr-2">
          <button
            v-if="isEditing && !error"
            type="button"
            class="p-1 focus:outline-none"
            @click="handleConfirm"
            :disabled="disabled"
          >
            <i class="material-icons text-green-04">check</i>
          </button>
          <button 
            type="button"
            class="p-1 focus:outline-none"
            @click="handleCancel"
            :disabled="disabled"
          >
            <i class="material-icons text-red-04">close</i>
          </button>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { ref, watch, onMounted } from 'vue'
  
  export default {
    name: 'CustomConditionInput',
    props: {
      modelValue: {
        type: [String, Number],
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      placeholder: {
        type: String,
        default: 'Enter value'
      },
      ref: {
        type: String,
        default: null
      },
      error: {
        type: Boolean,
        default: false
      }
    },
    emits: ['update:modelValue', 'confirm', 'cancel'],
    
    setup(props, { emit }) {
      const inputValue = ref(props.modelValue)
      const isFocused = ref(false)
      const isEditing = ref(false)
  
      watch(() => props.modelValue, (newVal) => {
        inputValue.value = newVal
      })
  
      const handleInput = () => {
        isEditing.value = true
        emit('update:modelValue', inputValue.value)
      }
  
      const handleConfirm = () => {
        isEditing.value = false
        isFocused.value = false
        emit('confirm', inputValue.value)
      }
  
      const handleCancel = () => {
        isEditing.value = false
        inputValue.value = props.modelValue
        emit('cancel')
        isFocused.value = false
      }
  
      function onBlur(event) {
        setTimeout(() => {
          if (document.activeElement && !event.target.contains(document.activeElement)) {
            isFocused.value = false
          }
        }, 100)
      }
  
      return {
        inputValue,
        isFocused,
        isEditing,
        handleInput,
        handleConfirm,
        handleCancel,
        onBlur
      }
    }
  }
  </script>